@import url("https://fonts.googleapis.com/css2?family=Joti+One:wght@400&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Holtwood+One+SC:wght@400&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Jockey+One:wght@400&display=swap");
@import url("https://fonts.googleapis.com/css2?family=JetBrains+Mono:wght@400&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Gemunu+Libre:wght@400&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Jomhuria:wght@400&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Public+Sans:wght@400&display=swap");
body {
  margin: 0;
  line-height: normal;
}
:root {
  /* fonts */
  --font-jomhuria: Jomhuria;
  --font-holtwood-one-sc: "Holtwood One SC";
  --font-joti-one: "Joti One";
  --font-public-sans: "Public Sans";
  --font-jockey-one: "Jockey One";
  --font-gemunu-libre: "Gemunu Libre";
  --font-jetbrains-mono: "JetBrains Mono";

  /* font sizes */
  --font-size-62xl: 81px;
  --font-size-5xl: 24px;
  --font-size-77xl: 96px;
  --font-size-17xl: 36px;
  --font-size-29xl: 48px;
  --font-size-13xl: 32px;
  --font-size-45xl: 64px;

  /* Colors */
  --color-burlywood: #ffcb8e;
  --color-white: #fff;
  --color-snow: #fffbfb;
  --color-black: #000;
  --color-darkslategray: #464646;

  /* border radiuses */
  --br-71xl: 90px;
  --br-1181xl: 1200px;
  --br-6xl: 25px;
  --br-68xl: 87px;
}
